<template>
<div>
  <svg :width="this.settings.controllers.find(e => e.name === 'width').value" :height="height" id="logo" :style="background">
    <g :transform="transformIcon" id="icon">
    </g>
    <g id="text" :transform="transformText">
    </g>
  </svg>
  <div class="info">Press <strong>TAB</strong> to toggle settings</div>
  </div>
</template>
<script>

import * as d3 from 'd3'
import * as random from 'canvas-sketch-util/random'
import { downloadDataUri, map } from '@pangenerator/utils'

import { getSVGString, svgString2Image } from '../logic/svg2png'
import layouts from '../logic/layouts'
export default {
  props: ['settings'],
  data () {
    return {
      // maxLevel: 5,
      noiseFrequency: 1000,
      rectangles: [],
      seed: 0
    }
  },
  computed: {
    background () {
      return this.settings.controllers.find(e => e.name === 'transparent').value ? 'background:none;' : `background:${this.settings.controllers.find(e => e.name === 'invert').value ? '#000' : '#fff'}`
    },
    layout () {
      const l = this.settings.controllers.find(e => e.name === 'variant').value
      const layout = layouts(this.settings.controllers.find(e => e.name === 'width').value, l)
      return layout
    },
    filename () {
      const d = new Date()
      return d.getFullYear().toString().substring(2, 4) + '' + (d.getMonth() + 1).toString().padStart(2, '0') + d.getDate().toString().padStart(2, '0') + 'open-future(' + this.seed + ')'
    },
    maxLevel () {
      const min = 2
      const max = 6
      if (this.settings.controllers.find(e => e.name === 'adaptative scale').value) {
        const w = this.settings.controllers.find(e => e.name === 'width')
        return min + map(this.settings.controllers.find(e => e.name === 'width').value, w.min, w.max, 0, max - min)
      } else {
        return 5
      }
    },
    height () {
      return this.layout.height
    },
    transformIcon () {
      const l = this.settings.controllers.find(e => e.name === 'variant').value
      return `translate(${this.layout.icon.x}, ${this.layout.icon.y})rotate(${l === 'rectangle' ? 0 : this.settings.controllers.find(e => e.name === 'rotation').value})`
    },
    transformText () {
      return `translate(${this.layout.text.x}, ${this.layout.text.y})scale(${this.layout.text.scale})`
    },
    arc () {
      const inner = this.layout.icon.inner
      const outer = this.layout.icon.outer
      const diff = outer - inner
      return d3.arc()
        .startAngle(d => d.x * Math.PI * 2 - 0.005)
        .endAngle(d => (d.x + d.width) * Math.PI * 2)
        .innerRadius(d => this.layout.icon.inner + d.y * diff)
        .outerRadius(d => this.layout.icon.inner + (d.y + d.height) * diff + 0.55)
    },
    colorScale () {
      const grayscale = this.$vui.get('invert')
        ? [this.color(190), this.color(255)]
        : [this.color(120), this.color(24)]
      return d3.scaleLinear()
        .domain([0, 255])
        .range(grayscale)
    }
  },
  async mounted () {
    this.seed = this.seed = Math.round(Math.random() * 100000)
    d3.xml(require('../assets/open_future_txt.svg')).then(
      data => {
        d3.select('g#text').node().append(data.documentElement)
      }
    )
    this.generateData()
    this.$vui.on('width', () => { this.generateData() })
    this.$vui.on('color', () => { this.generateLogo() })
    this.$vui.on('invert', () => { this.generateLogo() })
    this.$vui.on('adaptative scale', () => { this.generateData() })
    this.$vui.on('randomize', () => { this.seed = Math.round(Math.random() * 100000); this.generateData() })
    this.$vui.on('variant', (v) => { this.generateLogo() })
    this.$vui.on('accent', (v) => { this.generateLogo() })
    this.$vui.on('export SVG', () => {
      var svg = document.getElementById('logo')
      var serializer = new XMLSerializer()
      var source = serializer.serializeToString(svg)
      if (!source.match(/^<svg[^>]+xmlns="http:\/\/www\.w3\.org\/2000\/svg"/)) {
        source = source.replace(/^<svg/, '<svg xmlns="http://www.w3.org/2000/svg"')
      }
      if (!source.match(/^<svg[^>]+"http:\/\/www\.w3\.org\/1999\/xlink"/)) {
        source = source.replace(/^<svg/, '<svg xmlns:xlink="http://www.w3.org/1999/xlink"')
      }
      source = '<?xml version="1.0" standalone="no"?>\r\n' + source

      // convert svg source to URI data scheme.
      var url = 'data:image/svg+xml;charset=utf-8,' + encodeURIComponent(source)
      downloadDataUri({
        data: url,
        filename: this.filename + '.svg'
      })
    })
    this.$vui.on('export PNG', () => {
      const svgString = getSVGString(d3.select('svg#logo').node())
      svgString2Image(svgString, this.settings.controllers.find(e => e.name === 'width').value, this.height, 'png', this.$vui.get('transparent'), (url) => {
        downloadDataUri({
          data: url,
          filename: this.filename + '.png'
        })
      })
    })
  },
  beforeDestroy () {
    this.$vui.off('width')
    this.$vui.off('color')
    this.$vui.off('invert')
    this.$vui.off('adaptative scale')
    this.$vui.off('randomize')
    this.$vui.off('variant')
    this.$vui.off('accent')
    this.$vui.off('export SVG')
    this.$vui.off('export PNG')
  },
  methods: {
    generateLogo () {
      d3.select('svg#logo').select('g#icon').selectAll('rect').remove()
      d3.select('svg#logo').select('g#icon').selectAll('path').remove()
      if (this.$vui.get('variant') !== 'rectangle') {
        d3.select('svg#logo')
          .select('g#icon')
          .selectAll('path')
          .data(this.rectangles)
          .enter()
          .append('path')
          .attr('d', this.arc)
          // .attr('stroke-width', 0.4)
          // .attr('stroke', d => d.color < 254 ? this.colorScale(d.color) : this.$vui.get('color'))
          .attr('fill', d => d.accent || !this.$vui.get('accent') ? this.colorScale(d.color) : this.$vui.get('color'))
      } else {
        const h = this.height
        const w = this.settings.controllers.find(e => e.name === 'width').value
        const m = 0 // w / 25.0
        console.log(h)
        d3.select('svg#logo')
          .select('g#icon')
          .selectAll('rect')
          .data(this.rectangles)
          .enter()
          .append('rect')
          .attr('x', d => m + d.x * (w - 2 * m) - w / 2)
          .attr('y', d => d.y * h)
          .attr('stroke', 'none')
          .attr('width', d => d.width * (w - 2 * m) + 0.5)
          .attr('height', d => d.height * h + 0.5)
          // .attr('stroke-width', 0.4)
          // .attr('stroke', d => d.color < 254 ? this.colorScale(d.color) : this.$vui.get('color'))
          .attr('fill', d => d.accent || !this.$vui.get('accent') ? this.colorScale(d.color) : this.$vui.get('color'))
      }
      d3.select('svg#logo').select('g#text').select('svg').selectAll('path').attr('fill', this.$vui.get('invert') ? '#fff' : '#000')
    },
    generateData () {
      // console.log(this.maxLevel)
      this.rectangles = []
      random.setSeed(this.seed)

      const divide = (R, vertical, ratio) => {
        return vertical
          ? [
            { x: R.x, y: R.y, width: R.width * ratio, height: R.height },
            { x: R.x + R.width * ratio, y: R.y, width: R.width * (1 - ratio), height: R.height }
          ]
          : [
            { x: R.x, y: R.y, width: R.width, height: R.height * ratio },
            { x: R.x, y: R.y + R.height * ratio, width: R.width, height: R.height * (1 - ratio) }
          ]
      }
      const subdivide = (R, level) => {
        const probability = 1 - R.x
        if (level < this.maxLevel) {
          const s1 = divide(R, true, 0.5)
          const s2a = divide(s1[0], false, 0.5)
          const s2b = divide(s1[1], false, 0.5)
          const subRects = [s2a[0], s2a[1], s2b[0], s2b[1]]
          if (random.value() < probability) {
            for (let i = 0; i < 4; i++) {
              subdivide(subRects[i], level + 1)
            }
          } else {
            addRect(R)
          }
        } else {
          addRect(R)
        }
      }

      const addRect = (R) => {
        const steps = 10
        const stepSize = R.width / steps
        for (let i = 0; i < steps; i++) {

          const color = ((random.noise2D(R.x, R.y, this.noiseFrequency) + 1) / 2 < 0.5) ? 255 * (i / steps) : 255 * (1 - (i / steps))
          const xpos = R.x + i * stepSize
          const accent = random.value() > R.x - 0.4
          if (xpos > 0.5) {
            this.rectangles.push({ x: xpos, y: R.y, width: stepSize, height: R.height, color: color, accent })
          } else {
            const n = (random.noise2D(xpos, R.y, this.noiseFrequency * 0.1) + 1) / 2
            const m = map((1 - xpos), 0, 1, 0.1, 0.85) // better fallof
            // console.log( R.y )
            // && stepSize < 0.09
            if (n > (1 - xpos) * m && xpos !== 0) {
              this.rectangles.push({ x: xpos, y: R.y, width: stepSize, height: R.height, color: color, accent })
            }
          }
        }
      }
      // start
      subdivide({ x: 0, y: 0, width: 1, height: 1 }, 0)
      this.generateLogo()
    },
    color (val) {
      return '#' + val.toString(16) + val.toString(16) + val.toString(16)
    }
  }
}
</script>

<style lang="scss" scoped>
  #logo{
    background: #fff;
    -webkit-box-shadow: 4px 4px 0px 0px rgba(141,141,141,1);
    -moz-box-shadow: 4px 4px 0px 0px rgba(141,141,141,1);
    box-shadow: 4px 4px 0px 0px rgba(141,141,141,1);
    border: 1px solid #777;
  }
  .info {
    font-size: 0.75em;
    color: #333;
    position: absolute;
    bottom: 0;
    left:0;
    font-family: Helvetica, Arial, sans-serif;
    margin: 0 0 0.5em 0.6em;
    padding:0;
  }
</style>
