import proportions from '../assets/proportions.json'

export default (width, layout) => {
  return {
    height: width * proportions[layout].height / 1000,
    icon: {
      x: width * proportions[layout].circleX / 1000,
      y: width * proportions[layout].circleY / 1000,
      inner: width * proportions[layout].innerRadius / 1000,
      outer: width * proportions[layout].outerRadius / 1000
    },
    text: {
      x: width * proportions[layout].txtX / 1000,
      y: width * proportions[layout].txtY / 1000,
      scale: (width * proportions[layout].txtWidth / 1000) / 100
    }
  }
}
